import axios from "../../utils/axios";

//  upToken
export function qiuniuUpToken(query) {
    return axios({
        url: "/admin/file/upload/qiuniuUpToken",
        method: "get",
        params: query
    });
}

// 
export function uploadFile(url, formdata) {
    return axios({
        url: url,
        method: "post",
        data: formdata
    });
}
