import axios from "../../utils/axios";

// 
export function fileResourceTagList(query) {
    return axios({
        url: "/admin/file/resource_tag/index",
        method: "get",
        params: query
    });
}

// 
export function fileResourceTagAdd(data) {
    return axios({
        url: "/admin/file/resource_tag/add",
        method: "post",
        data: data
    });
}
