import axios from "../../utils/axios";

// 
export function fileResourceList(query) {
    return axios({
        url: "/admin/file/resource/index",
        method: "get",
        params: query
    });
}
// 
export function fileResourceAdd(data) {
    return axios({
        url: "/admin/file/resource/add",
        method: "POST",
        data: data
    });
}
